@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  outline: none;
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
}
body{
  font-size: 14px;
  background: rgb(247,162,200);
  background: radial-gradient(circle, rgba(247,162,200,1) 0%, rgba(125,182,249,1) 100%);
}
::selection{
  color: #fff;
  background: #0074D9;
}

.content {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.content_asign{
  width: 1000px;
  max-width: 100%;
  background: #fff;
  border-radius: 5px;
  padding: 50px 30px 10px 30px;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  text-align: center;
}

.content_asign header{
  font-size: 35px;
  font-weight: 600;
  margin: 0 0 30px 0;
}

.form-outer {
  overflow: scroll;
  flex: 1 1;
}

.form-outer::-webkit-scrollbar {
  display: none;
}

.next-step {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.next-step button {
  flex: 1 1;
  height: 50px;
  border: none;
  background-color: #0074D9;
  border-radius: 10px;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
}

.next-step button:hover {
  background-color: black;
}

.next-step .pre-btn {
  margin-right: 10px;
}

#submit-btn {
  display: none;
}

.assign-button, .end a {
  border: none;
  background-color: #0074D9;
  width: 200px;
  height: 60px;
  padding: 10px;
  border-radius: 10px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.assign-button:hover, .end a:hover {
  background-color: black;
}

.start, .end {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.start h1, .end h1 {
  text-align: center;
}

.start button {
  margin-top: 10px;
}

.end a{
  text-decoration: none;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
}

@media only screen and (max-width: 1024px) {
  .content_asign{
    height: calc(100vh);
  }
}

@media only screen and (max-width: 600px) {
  .content_asign{
    padding: 30px 10px 10px 10px;
  }
}

.progress-bar{
  display: flex;
  margin: 40px 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.progress-bar .step{
  text-align: center;
  width: 100%;
  position: relative;
}
.progress-bar .step p{
  font-weight: 500;
  font-size: 18px;
  color: #000;
  margin-bottom: 8px;
}
.progress-bar .step .bullet{
  height: 25px;
  width: 25px;
  border: 2px solid #000;
  display: inline-block;
  border-radius: 50%;
  position: relative;
  transition: 0.2s;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
}
.progress-bar .step .bullet.active{
  border-color: #d43f8d;
  background: #d43f8d;
}
.progress-bar .step .bullet span{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.progress-bar .step .bullet.active span{
  display: none;
}
.progress-bar .step .bullet:before,
.progress-bar .step .bullet:after{
  position: absolute;
  content: '';
  bottom: 11px;
  left: 50px;
  height: 3px;
  width: 235px;
  background: #262626;
}
.progress-bar .step .bullet.active:after{
  background: #d43f8d;
  transform: scaleX(0);
  transform-origin: left;
  -webkit-animation: animate 0.3s linear forwards;
          animation: animate 0.3s linear forwards;
}
@-webkit-keyframes animate {
  100%{
    transform: scaleX(1);
  }
}
@keyframes animate {
  100%{
    transform: scaleX(1);
  }
}
.progress-bar .step:last-child .bullet:before,
.progress-bar .step:last-child .bullet:after{
  display: none;
}
.progress-bar .step p.active{
  color: #d43f8d;
  transition: 0.2s linear;
}
.progress-bar .step .checked{
  font-size: 15px;
  color: #fff;
}

@media only screen and (max-width: 1024px) {
  .progress-bar .step .bullet:before,
  .progress-bar .step .bullet:after{
    width: 242px;
  }
}

@media only screen and (max-width: 768px) {
  .progress-bar .step .bullet:before,
  .progress-bar .step .bullet:after{
    width: 155px;
  }
}

@media only screen and (max-width: 600px) {
  .progress-bar .step .bullet:before,
  .progress-bar .step .bullet:after{
    width: 12vw;
  }
}

@media only screen and (max-width: 400px) {
  .progress-bar .step p {
    display: none;
  }
  .progress-bar .step .bullet:before,
  .progress-bar .step .bullet:after{
    width: 40px;
  }
}

@media only screen and (max-width: 320px) {
  .progress-bar .step .bullet:before,
  .progress-bar .step .bullet:after{
    width: 20px;
  }
}
.page{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.page-self {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 18px;
}

.page-self__item {
  margin-bottom: 10px;
  width: 80%;
}

.red {
  color: red;
}

.field{
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.field-first {
  margin-top: 50px;
}

.field-radio {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  align-items: center;
}

.field-radio input {
  height: 20px;
  width: 20px !important;
  margin-right: 10px;
}

.space {
  width: 20px;
}

.label{
  font-weight: 500;
  text-align: left;
}

.field input{
  height: 40px;
  border: 1px solid lightgrey;
  border-radius: 5px;
  padding-left: 15px;
  font-size: 14px;
}

.field textarea {
  border: 1px solid lightgrey;
  border-radius: 5px;
  padding: 15px;
  resize: none;
}
